import { Link } from "react-router-dom";
import { navLinks } from "../../utils/Constants";
import useMediaQuery from "../../hooks/useMediaQuery";
import MobileBrandList from "../../components/MobileBrandList";
import { useSidebar } from "../../contexts/sidebarContext";

const FallbackNav = () => {
  const maxMd = useMediaQuery("(min-width: 768px)");
  const maxXs = useMediaQuery("(min-width: 320px)");
  const { openSidebar } = useSidebar();

  const handleClick = (title) => {
    if (title === "BRANDS") openSidebar(<MobileBrandList />);
  };

  return !maxMd ? (
    <nav>
      <ul className="container max-w-[400px] pt-2 pb-1 flex items-center justify-between gap-4 max-sm:gap-2">
        {navLinks.map((link) => (
          <li
            key={link.title}
            className={`block capitalize ${!maxXs && "text-xs"}`}
          >
            <Link to={link.url} onClick={() => handleClick(link.title)}>
              {link.title}
            </Link>
          </li>
        ))}
      </ul>
    </nav>
  ) : (
    <></>
  );
};

export default FallbackNav;
